<!--  签约居民信息详情 -->
<template>
  <div class="table-detail-box sign-detail flex-column" v-if="signInfo">
   <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb>
        <span slot="custom">{{signInfo.familyPerson&&signInfo.familyPerson.userName}}</span>
      </Breadcrumb>
    </div>
    <!-- 表格 -->
    <div class="inner-box flex-overflow">
      <table class="default-table resident-table" cellspacing="0" cellspadding="0">
        <template v-if="signInfo.familyPerson">
          <tr>
            <th>姓名</th>
            <td>
              <span>{{signInfo.familyPersonsignInfo.familyPerson.userName||EMPTYTEXT}}</span>
              <el-link type="primary" :underline="false" @click="handleView(signInfo.familyPerson.personId,signInfo.familyPerson.userName)"><cc-svg-icon icon-class="magnifier"></cc-svg-icon> 查看协议</el-link>
            </td>
          </tr>
          <tr>
            <th>性别</th>
            <td>{{signInfo.familyPerson.sexTypeStr||EMPTYTEXT}}</td>
          </tr>
          <tr>
            <th>身份证号</th>
            <td>{{signInfo.familyPerson.idNo||EMPTYTEXT}}</td>
          </tr>
        </template>
        <tr>
          <th>服务包信息</th>
          <td class="tstd">
            <table cellspacing="0" cellspadding="0">
              <tr>
                <td>名称</td>
                <td>服务包类型</td>
                <td class="large">服务期限</td>
                <td>服务价格</td>
                <td>优惠价格</td>
                <td>生效区域</td>
                <td>是否过期</td>
              </tr>
              <template v-if="signInfo.packages&&signInfo.packages.length>0"> 
                <tr v-for="(item,index) in signInfo.packages" :key="index">
                  <td>{{item.packageName||EMPTYTEXT}}</td>
                  <td>{{item.packageType===1?'收费':item.packageType===2?'免费':EMPTYTEXT}}</td>
                  <td class="large">{{item.createDateStr+EMPTYTEXT+item.invalidDateStr}}</td>
                  <td>{{item.packageOriginalPrice||EMPTYTEXT}}</td>
                  <td>{{item.packageDiscountPrice||EMPTYTEXT}}</td>
                  <td>{{item.positionName||EMPTYTEXT}}</td>
                  <td>{{item.invalidStatusStr||EMPTYTEXT}}</td>
                </tr>
              </template>
              <template v-else>
                  <tr>
                     <td colspan="7">{{EMPTYTEXT}}</td>
                  </tr>
              </template>
            </table>
          </td>
        </tr>
        <tr>
        </tr>
        <tr>
          <th>总服务价格</th>
          <td>{{signInfo.totalOriginalPrice||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>总优惠价格</th>
          <td>{{signInfo.totalDiscountPrice||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>所属机构</th>
          <td>{{signInfo.registerOrganization||EMPTYTEXT}}</td>
        </tr>
        <tr>
          <th>服务包名称</th>
          <td class="tstd">
            <table cellspacing="0" cellspadding="0" class="tTable">
              <tr>
                <td>服务项目名称</td>
                <td>执行项</td>
                <td>履约类型</td>
                <td>履约格式（每）</td>
                <td>周期履约次数</td>
                <td>已服务次数</td>
              </tr>
            </table>
          </td>
        </tr>
        <template v-if="signInfo.packages&&signInfo.packages.length>0">
          <tr v-for="(item,index) in signInfo.packages" :key="index">
            <th>{{item.packageName||EMPTYTEXT}}</th>
            <td class="tstd">
              <table cellspacing="0" cellspadding="0" class="tTable">
                <template v-if="item.items.length>0">
                  <tr v-for="(it,i) in item.items" :key="i">
                    <template v-if="!it.parentItemId">
                      <td>{{it.itemName||EMPTYTEXT}}</td>
                      <td :class="it.appointType===16?'flex-column':''">
                        <template v-if="it.appointType===16">                       
                          <td v-for="(k,ii) in forMatName(item.items,it.itemId)" :key="ii">{{k.itemName}}</td>
                        </template>
                        <template v-else>-</template>
                      </td>
                      <td>{{appointType[it.appointType]}}</td>
                      <td>{{appointFormat[it.appointFormat]}}</td>
                      <td>{{it.appointNum}}</td>
                      <td>已服务次数</td>
                    </template>
                  </tr>
                </template>
                <template v-else>
                    <tr>
                      <td>{{EMPTYTEXT}}</td>
                    </tr>
                </template>
              </table>
            </td>
          </tr>
        </template>
        <template v-else>
          <tr>
            <th>{{EMPTYTEXT}}</th>
            <td>{{EMPTYTEXT}}</td>
          </tr>
        </template>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name:'sign-detail',
  data() {
    return {
      signInfo:null, //签约居民信息
      appointFormat:{
        1:'年',
        2:'季度',
        4:'月',
        8:'日'
      },//履约格式
      appointType:{
        1:'测量类',
        2:'标记类',
        4:'公共卫生类',
        8:'指导类',
        16:'组合类'
      }//履约类型
    }
  },
  created() {
    this.getDetailInfo() //获取签约居民信息详情
  },
  methods: {
    //去协议列表
    handleView(id,name){
      this.$router.push({
        path: '/resident-manage/family-archives/agreement-list',
        query:{
          id:id,
          name:name
        }
      })
    },
    //获取执行项
    forMatName(arr,item){
     let result =  arr.filter(it=>{
       return  it.parentItemId===item
      })
      return result
    },
    //获取签约居民信息详情
    getDetailInfo(){
      this.$emit("viewLoad", true);
      this.$http.get(process.env.VUE_APP_API + '/family/service/getDtoByServiceId?serviceId='+this.$route.query.id).then(res => {
        if (res.code===200) {
          this.signInfo = res.data
        } else {
          this.$message.error(res.msg);
        }
        this.$emit("viewLoad", false);
      })
      .catch(error => {
        this.$emit("viewLoad", false);
        this.$message.error(error);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
@import "~@/assets/styles/table-default";
.sign-detail{
  .sign-table{
    width:100%;
    max-width:1320px;
    .tstd{
      display:flex;
      flex:1;
      .leftbox{
        width:30%;
      }
      .rightbox{
        display:flex;
        p{
          width:33%;
        }
      }
    }
  }
}
</style>
